/* ===================================================================
 * # site header
 *
 * ------------------------------------------------------------------- */
.s-wrap {
  position: relative;
}

.s-header {
  z-index: 100;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  width: 300px;
  background-color: #151515;
  padding-top: 6.4rem;
  padding-right: 3.2rem;
  padding-left: 4.4rem;
  overflow-y: auto;
  overflow-x: hidden;
  vertical-align: top;
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
}

/* -------------------------------------------------------------------
 * ## header logo
 * ------------------------------------------------------------------- */
.header__logo a {
  display: block;
  margin: 0;
  padding: 0;
  outline: 0;
  border: none;
}

.header__logo img {
  width: 192px;
  height: 30px;
  margin: -1px;
}

/* -------------------------------------------------------------------
 * ## header main navigation
 * ------------------------------------------------------------------- */
.header__nav-wrap {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  -webkit-flex: 1 1 0%;
  -ms-flex: 1 1 0%;
  flex: 1 1 0%;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  max-width: 100%;
  background-color: #151515;
  margin-top: 3.2rem;
}

.header__nav-wrap a {
  color: rgba(255, 255, 255, 0.6);
}

.header__nav {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Heebo", sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.header__nav li {
  display: block;
  padding: 0 2rem 0 0;
  width: 100%;
  position: relative;
}

.header__nav li a {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  line-height: 4.4rem;
}

.header__nav li.has-children {
  padding-right: 3.3rem;
}

.header__nav li.has-children > a::after {
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  border-right: 1px solid rgba(255, 255, 255, 0.4);
  content: "";
  display: block;
  height: 5px;
  width: 5px;
  margin-top: -4px;
  pointer-events: none;
  position: absolute;
  right: 1.8rem;
  top: 21px;
  -webkit-transform-origin: 66% 66%;
  -ms-transform-origin: 66% 66%;
  transform-origin: 66% 66%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  transition: all 0.5s;
  
}

.header__nav li.has-children > a.sub-menu-is-open::after {
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg);
  
  
}



.sub-menu {
  display: block;
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0;
  padding: 0.8rem 0 2rem 0.8rem;
  
  
}
.sub-menu li a {
  line-height: 3.2rem;
}


.header__nav li:hover > a,
.header__nav li:focus > a {
  color: #ffffff;
}

.header__nav li:hover li,
.header__nav li:focus li {
  background: transparent;
}

.header__nav li > a.current {
  color: #ffffff;
}




/* ------------------------------------------------------------------- 
 * ## menu toggle
 * ------------------------------------------------------------------- */
.header__menu-toggle {
  display: none;
  height: 42px;
  width: 42px;
  line-height: 42px;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: rgba(255, 255, 255, 0.5);
  transition: all 0.3s;
  position: absolute;
  right: 4rem;
  top: 18px;
}

.header__menu-toggle:hover,
.header__menu-toggle:focus {
  color: #ffffff;
}

.header__menu-toggle span {
  display: block;
  width: 16px;
  height: 2px;
  margin-top: -1px;
  background-color: #ffffff;
  transition: all 0.5s;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  position: absolute;
  top: 50%;
  left: 9px;
  right: auto;
  bottom: auto;
}

.header__menu-toggle span::before,
.header__menu-toggle span::after {
  content: "";
  width: 24px;
  height: 100%;
  background-color: inherit;
  position: absolute;
  left: 0;
  transition: all 0.5s;
}

.header__menu-toggle span::before {
  top: -8px;
}

.header__menu-toggle span::after {
  bottom: -8px;
}

.header__menu-toggle.is-clicked span {
  background-color: rgba(255, 255, 255, 0);
  transition: all 0.1s;
}

.header__menu-toggle.is-clicked span::before,
.header__menu-toggle.is-clicked span::after {
  background-color: white;
}

.header__menu-toggle.is-clicked span::before {
  top: 0;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}

.header__menu-toggle.is-clicked span::after {
  bottom: 0;
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg);
}

/* -------------------------------------------------------------------
 * ## header social
 * ------------------------------------------------------------------- */
.header__social {
  list-style: none;
  margin: 4rem 0 0 0;
  padding: 0 0 4.4rem 0;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

.header__social li {
  margin-right: 2px;
}

.header__social li:last-child {
  margin-right: 0;
}

.header__social a {
  display: block;
  background-color: rgba(255, 255, 255, 0.04);
  color: rgba(255, 255, 255, 0.5);
  height: 3.6rem;
  width: 3.6rem;
  border-radius: 50%;
  position: relative;
}

.header__social a::after {
  z-index: 1;
  content: "";
  display: block;
  height: 1.4rem;
  width: 1.4rem;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  transition: all 0.3s;
  position: absolute;
  left: 50%;
  right: 0;
  top: 50%;
}

.header__social a:hover::after {
  height: 1.8rem;
  width: 1.8rem;
}

.header__social span {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  border: 0;
  position: absolute;
}

.header__social .ss-facebook a::after {
  background-image: url(./images/icons/social/facebook.svg);
}

.header__social .ss-twitter a::after {
  background-image: url(./images/icons/social/icon-twitter.svg);
}

.header__social .ss-dribbble a::after {
  background-image: url(./images/icons/social/icon-dribbble.svg);
}

.header__social .ss-youtube a::after {
  background-image: url(./images/icons/social/youtube.svg);
}

.header__social .ss-instagram a::after {
  background-image: url(./images/icons/social/instagram.svg);
}

.header__social .ss-email a::after {
  background-image: url(./images/icons/social/mail.svg);
}

.header__social .ss-linkedin a::after {
  background-image: url(./images/icons/social/linkedin.svg);
}


.header__social .ss-rss a::after {
  background-image: url(./images/icons/social/icon-rss.svg);
}

/* -------------------------------------------------------------------
 * ## header search trigger
 * ------------------------------------------------------------------- */
.header__search-trigger {
  z-index: 1;
  display: block;
  color: #000000;
  height: 16px;
  width: 16px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  background-image: url(./images/icons/icon-search.svg);
  position: fixed;
  right: 40px;
  top: 6.4rem;
}

.header__search-trigger::before {
  content: "";
  display: block;
  width: 1px;
  height: calc(100vh - 200px);
  background-color: rgba(0, 0, 0, 0.1);
  position: fixed;
  right: 48px;
  top: auto;
  bottom: 0;
}

.header__search-trigger::after {
  content: "Search";
  font-family: "Heebo", sans-serif;
  font-weight: 500;
  font-size: 11px;
  line-height: 1.6rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  width: auto;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-transform-origin: 1.6rem 100%;
  transform-origin: 1.6rem 100%;
  position: absolute;
  top: 72px;
  left: 0;
}

/* ------------------------------------------------------------------- 
 * ## header-search
 * ------------------------------------------------------------------- */
.header__search {
  z-index: 400;
  display: block;
  text-align: center;
  background-color: #ffffff;
  transition: all 0.3s;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header__search-form {
  width: 100%;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
  position: absolute;
  top: 30%;
}

.header__search-form label {
  color: #000000;
}



.header__search-form input[type="search"] {
  background-color: transparent;
  color: #000000;
  height: auto;
  width: 100%;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 700;
  font-size: 6rem;
  line-height: 1.6;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
  max-width: 680px;
  padding-top: 0.8rem !important;
  padding-bottom: 0.8rem !important;
  margin: 0 auto;
  text-align: center;
}

.header__search-form input[type="search"]::-webkit-input-placeholder {

  /* WebKit, Blink, Edge */
  color: #000000;
}

.header__search-form input[type="search"]:-moz-placeholder {

  /* Mozilla Firefox 4 to 18 */
  color: #000000;
  opacity: 1;
}

.header__search-form input[type="search"]::-moz-placeholder {

  /* Mozilla Firefox 19+ */
  color: #000000;
  opacity: 1;
}

.header__search-form input[type="search"]:-ms-input-placeholder {

  /* Internet Explorer 10-11 */
  color: #000000;
}

.header__search-form input[type="search"]::-ms-input-placeholder {

  /* Microsoft Edge */
  color: #000000;
}

.header__search-form input[type="search"]::placeholder {

  /* Most modern browsers support this now. */
  color: #000000;
}

.header__search-form input[type="search"].placeholder {
  color: #000000 !important;
}

.header__search-form input[type="search"]:focus {
  outline: none;
}

.header__search-form input[type="submit"] {
  display: none;
}

.header__search-form .btn {
  
  
  font-size: 1.6rem;
  margin-top: 3.2rem;
  text-align: center;
  color: #000000;
  background-color: transparent;
  border: 0.2rem solid black;
  

}
.header__search-form .btn:hover {
  
  
  
  color:white;
  background-color: black;
  
  

}
body.search-is-visible {
  overflow: hidden;
}

.search-is-visible .header__search {
  opacity: 1;
