 * ------------------------------------------------------------------- */
.footer__copyright span {
  display: inline-block;
}

.footer__copyright span::after {
  content: "|";
  display: inline-block;
  padding: 0 1.2rem 0 1.4rem;
  color: rgba(0, 0, 0, 0.3);
}

.footer__copyright span:last-child::after {
  display: none;
}

/* ------------------------------------------------------------------- 
 * ## go to top
 * ------------------------------------------------------------------- */
.go-top {
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translate3d(0, 200%, 0);
  transform: translate3d(0, 200%, 0);
  transition: all 0.8s;
  position: fixed;
  bottom: 4rem;
  right: 2rem;
}

.go-top button,
.go-top button:visited {
  text-decoration: none;
  border: none;
  display: block;
  height: 6rem;
  width: 6rem;
  line-height: 6rem;
  text-align: center;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
  transition: all 0.3s;
  background-image: url(./images/icons/icon-arrow-up.svg);
  background-color: #000000;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 7px 14px;
}

.go-top.link-is-visible {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

/* ------------------------------------------------------------------- 
 * responsive:
 * site content & footer
 * ------------------------------------------------------------------- */
@media screen and (max-width:1600px) {
  .s-content,
  .s-footer {
    padding-left: 320px;
  }

  .ant-message {
    padding-left: 320px;
  }

}

@media screen and (max-width:1200px) {
  .s-content,
  .s-footer {
    padding-left: 290px;
    padding-right: 80px;
  }

  .ant-message {
    padding-left: 290px;
    padding-right: 80px;
  }
}

@media screen and (max-width:1100px) {
  .s-content,
  .s-footer {
    padding-right: 0;
    padding-left: 0;
  }

  .ant-message {
    padding-right: 0;
    padding-left: 0;
    padding-top: 10.2rem;
  }

  

  .s-content {
    padding-top: 14.2rem;
  }

  .content__page {
    padding-bottom: 8rem;
  }

  .go-top {
    bottom: 0;
    right: 0;
  }

}

.loading {
  text-align: center;
  margin-bottom: 100vh;
}

.anticon-loading {
  font-size: 25px;
  border: 8px solid white;
  border-radius: 50%;
  background: white;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.6);
}




@media screen and (max-width:600px) {
  .s-content {
    padding-top: 12.6rem;
  }

  .ant-message {
    padding-top: 12.6rem;
  }

  .content__page {
    padding-bottom: 4rem;
  }

  .footer__copyright span {
    display: block;
  }

  .footer__copyright span::after {
    display: none;
  }

}



/* ===================================================================
 * # blog styles and blog components
 *
 * ------------------------------------------------------------------- */
.entry__media,
.entry__post-thumb {
  position: relative;
}

@media screen and (max-width:400px) {
  .entry__post-thumb img {
    max-height: 330px;
    max-width: 100%;

  }
}

@media screen and (max-width:500px) {
  .entry__post-thumb img {
    max-height: 340px;
  }
}

@media screen and (max-width:600px) {
  .entry__post-thumb img {
    max-height: 350px;
  }
}

@media screen and (max-width:700px) {
  .entry__post-thumb img {
    max-height: 355px;
  }
}

@media screen and (max-width:1200px) {
  .entry__post-thumb img {
    max-height: 365px;
  }
}

@media screen and (max-width:1600px) {
  .entry__post-thumb img {
    max-height: 393px;
  }
}
/* -------------------------------------------------------------------
 * ## entry header meta
 * ------------------------------------------------------------------- */
.entry__header-meta {
  list-style: none;
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1.778;
  margin-left: 0;
  color: #000000;
}

.entry__header-meta li {
  display: inline-block;
  padding-left: 0;
  margin: 0 0.8rem;
  
}

.entry__header-meta .entry__meta-cat {
  margin-right: 10%;
}

.entry__header-meta a {
  color: rgba(0, 0, 0, 0.7);
}

.entry__header-meta a:hover,
.entry__header-meta a:focus {
  color: #000000;
}

.entry__header-meta .cat-links a::after {
  content: ", ";
}

.entry__header-meta .cat-links a:last-child::after {
  display: none;
}

/* -------------------------------------------------------------------
 * ## post tags
 * ------------------------------------------------------------------- */
.entry__tags {
  margin-top: 6rem;
  font-family: "Heebo", sans-serif;
}

.entry__tags span {
  font-weight: 500;
  color: #000000;
  padding-right: 56px;
  margin-right: 1.6rem;
  position: relative;
}

.entry__tags span::after {
  display: inline-block;
  content: "";
  height: 1px;
  width: 40px;
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 50%;
  right: 0;
}

.entry__tags .entry__tag-list {
  display: inline-block;
  padding-right: 0;
  margin-right: 0;
}

.entry__tags .entry__tag-list::after {
  display: none;
}

.entry__tags .entry__tag-list a {
  display: inline-block;
  font-weight: 400;
  font-size: 1.3rem;
  line-height: inherit;
  color: #1a1a1a;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  margin-right: 6px;
  margin-bottom: 8px;
  padding: 1px 12px;
}

.entry__tags .entry__tag-list a:hover,
.entry__tags .entry__tag-list a:focus {
  background-color: #000000;
  border-color: #000000;
  color: #ffffff;
}

.entry__tags .entry__tag-list a:last-child {
  margin-right: 0;
}

/* -------------------------------------------------------------------
 * ## blog single page navigation
 * ------------------------------------------------------------------- */
.entry__pagenav {
  margin-top: 10.2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  position: relative;
}

.entry__nav:after {
  content: "";
  display: table;
  clear: both;
}

.entry__nav span {
  display: block;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 400;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  color: #1a1a1a;
  margin-bottom: 1.6rem;
  transition: all 0.3s;
}

.entry__nav a {
  font-family: "Heebo", sans-serif;
  font-weight: 500;
  font-size: 3rem;
  line-height: 1.333;
  color: #000000;
}

.entry__nav a:hover span,
.entry__nav a:focus span {
  color: #33998a;
}

.entry__prev,
.entry__next {
  width: 50%;
  float: left;
  padding-right: 2rem;
}

/* -------------------------------------------------------------------
 * ## related articles
 * ------------------------------------------------------------------- */
.entry__related {
  margin-top: 10.2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  position: relative;
}

.entry__related h3 {
  margin-top: 0;
  margin-bottom: 3.2rem;
}

.entry__related h5 {
  margin-top: 0.8rem;
  font-size: 1.9rem;
  font-weight: 400;
  line-height: 2.8rem;
}

.related {
  list-style: none;
  margin-left: -4rem;
}

.related__item {
  float: left;
  width: 33.33333%;
  padding-left: 4rem;
  margin-bottom: 3.2rem;
}

.related__link {
  display: block;
  position: relative;
}

.related__link img {
  vertical-align: bottom;
}

.related__link::before {
  content: "";
  display: block;
  background: rgba(255, 255, 255, 0.2);
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s;
}

.related__link:hover::before {
  opacity: 1;
  visibility: visible;
}

/* ------------------------------------------------------------------- 
 * responsive:
 * blog styles and blog components
 * ------------------------------------------------------------------- */
@media screen and (max-width:800px) {
  .entry__header-meta {
    font-size: 1.7rem;
  }

  .entry__prev,
  .entry__next {
    width: auto;
    float: none;
    padding-right: 0;
  }

  .entry__nav [class*="entry__"] + [class*="entry__"] {
    margin-top: 2rem;
  }

  .related {
    margin-left: -4.4rem;
  }

  .related__item {
    padding-left: 4.4rem;
    width: 50%;
  }

  .related__item:nth-child(2n + 1) {
    clear: both;
  }

}

@media screen and (max-width:600px) {
  .related {
    margin-left: -2rem;
  }

  .related__item {
    padding-left: 2rem;
  }

}

@media screen and (max-width:460px) {
  .related {
    margin-left: 0;
  }

  .related__item {
    float: none;
    width: auto;
    padding-left: 0;
  }

}

/* -------------------------------------------------------------------
 * ## comments 
 * ------------------------------------------------------------------- */
.comments-wrap {
  padding: 6.4rem 0 0 0;
  background-color: #ffffff;
}

.comments-wrap h3 {
  margin-top: 0;
}

.comments-wrap h3 span {
  display: block;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 1.556;
  color: #7e7e7e;
}

/* comments
 * -------------------------------------------------- */
#comments {
  padding-top: 2rem;
  padding-bottom: 1.2rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

#comments ol,
#comments ul {
  list-style: none;
}

.commentlist {
  margin: 6rem 0 5.6rem;
  padding: 0;
}

.commentlist > .comment {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 2rem 0 0 0;
  padding-left: 14%;
}

.comment__avatar {
  position: absolute;
  left: 0;
  display: block;
}

.comment__avatar img {
  height: 6.4rem;
  width: 6.4rem;
  border-radius: 100%;
  vertical-align: bottom;
}

.comment__info {
  position: relative;
}

.comment__info .comment__author {
  font-family: "Heebo", sans-serif;
  font-weight: 500;
  font-size: 1.9rem;
  line-height: 1.684;
  font-style: normal;
  color: #000000;
}

.comment__meta {
  font-family: "IBM Plex Sans", sans-serif;
  font-style: normal;
  line-height: 2.4rem;
  display: block;
  color: #7e7e7e;
  text-align: right;
  width: 150px;
  position: absolute;
  top: 0;
  right: 0;
}

.comment__meta .comment__time {
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  color: #7e7e7e;
}

.comment__meta .comment__reply a {
  font-size: 1.3rem;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #000000;
}

.comment__text {
  clear: both;
  margin: 1.6rem 0 0 0;
  padding: 0 175px 0 0;
}

.comment ul.children {
  margin: 0;
  padding: 0;
}

.comment ul.children li {
  padding-left: 5%;
  margin-top: calc(3.2rem + 2rem);
  border-left: 1px solid rgba(0, 0, 0, 0.15);
}

/* comments form
 * ---------------------------- */
.comments-wrap .comment-respond {
  margin-top: 6.4rem;
}

.comment-respond form {
  padding-top: 6rem;
}

/* ------------------------------------------------------------------- 
 * responsive:
 * comments
 * ------------------------------------------------------------------- */
@media screen and (max-width:1000px) {
  .comment__avatar img {
    height: 6rem;
    width: 6rem;
  }

  .comment__meta {
    text-align: left;
    width: auto;
    position: static;
  }

  .comment__meta .comment__time,
  .comment__meta .comment__reply {
    display: inline-block;
  }

  .comment__meta .comment__time {
    margin-right: 12px;
  }

  .comment__text {
    padding: 0;
  }

  .comments-wrap .comment-respond {
    margin-top: 4.4rem;
  }

}

@media screen and (max-width:800px) {
  .comment__avatar img {
    margin-top: 0;
    height: 5.2rem;
    width: 5.2rem;
  }

}

@media screen and (max-width:600px) {
  .commentlist > .comment {
    padding-top: 0;
    padding-left: 0;
  }

  .comment__avatar {
    display: none;
  }

  .comment ul.children li {
    margin-top: calc(3.2rem + 0rem);
  }

}

@media screen and (max-width:500px) {
  .comment ul.children li {
    border-left: none;
  }

}

@media screen and (max-width:400px) {
  .comment-respond form {
    padding-top: 3.2rem;
  }

}

/* -------------------------------------------------------------------
 * ## format gallery
 * ------------------------------------------------------------------- */
.format-gallery .entry__thumb {
  overflow: hidden;
}

.format-gallery .slider {
  overflow: hidden;
}

.format-gallery .slick-dots {
  text-align: left;
  margin: 0;
  padding: 0 2rem 0 2.4rem;
  position: absolute;
  top: auto;
  bottom: 2.4rem;
  left: 0;
}

.format-gallery .slider__slides {
  cursor: pointer;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
}

.format-gallery .slider__slides.slick-initialized {
  opacity: 1;
  visibility: visible;
}

